const refs = {
  addDevBut: document.getElementById('addDeviceButton'),
  title: document.getElementById('ul'),
  body: document.querySelector('body'),
  productChoses: document.getElementById('productChoses'),
  resultTable: document.getElementById('resultTable'),
};
const formHtmlString = '<p>Dvice1</p>';
const tableHtmlString =
  '<tr><td></td><td></td><td></td><td></td><td></td></tr >';
console.dir(refs.resultTable);
refs.addDevBut.addEventListener('click', createDevice);
function createDevice(event) {
  event.preventDefault();
  refs.addDevBut.insertAdjacentHTML('afterend', formHtmlString);
  refs.resultTable.insertAdjacentHTML('beforeend', tableHtmlString);
  console.log('++++++');
}

refs.productChoses.addEventListener('change', addMainLicense);
function addMainLicense(event) {
  event.preventDefault();

  console.dir(refs.body.firstElementChild);
}

const customerCoisesForm = document.querySelector('form');
customerCoisesForm.addEventListener('change', kpBuilder);
const customerChoises = {};
function kpBuilder(event) {
  // const userAnswer = event.target.value;
  const indexOfQestion = event.target.name;
  //const customerChoisesArray = Object.values(customerChoises);
  console.dir(event);
  console.dir(event.target.type);
  console.dir(event.target.name);
  console.dir(event.target.checked);
  console.dir(event.target.value);

  if (event.target.type === 'radio') {
    console.log('RADIO');
    const userAnswer = event.target.value;
    customerChoises[indexOfQestion] = userAnswer;
  } else if (event.target.type === 'checkbox') {
    console.log('CHECKBOX');
    const userAnswer = event.target.checked;
    customerChoises[indexOfQestion] = userAnswer;
  } else if (event.target.type === 'text') {
    console.log('TEXT');
    const userAnswer = event.target.value;
    customerChoises[indexOfQestion] = userAnswer;
  }

  console.table(customerChoises);
  console.dir(customerCoisesForm.elements);
}

